import { NextRequest, NextResponse } from "next/server";
import { getPomodoroState, updatePomodoroState } from "@/lib/storage";
import { getSocketIO } from "@/lib/socket";
import {
  PomodoroStartRequest,
  PomodoroControlRequest,
  PomodoroApiResponse,
} from "@/types";

// GET - Get current Pomodoro status
export async function GET() {
  try {
    const pomodoroState = await getPomodoroState();

    // Calculate next break type
    const currentSession =
      pomodoroState.mode === "work"
        ? pomodoroState.session
        : pomodoroState.session - 1;
    const nextBreakType = currentSession % 4 === 0 ? "long" : "short";

    const response: PomodoroApiResponse = {
      success: true,
      data: {
        ...pomodoroState,
        nextBreakType,
      } as any,
    };

    return NextResponse.json(response);
  } catch (error) {
    console.error("‚ùå Get Pomodoro state error:", error);
    const response: PomodoroApiResponse = {
      success: false,
      error: "Failed to get Pomodoro state",
    };
    return NextResponse.json(response, { status: 500 });
  }
}

// POST - Start Pomodoro timer
export async function POST(request: NextRequest) {
  try {
    const body: PomodoroStartRequest = await request.json();
    const { username, duration = 25 } = body;

    if (!username) {
      const response: PomodoroApiResponse = {
        success: false,
        error: "Username is required",
      };
      return NextResponse.json(response, { status: 400 });
    }

    if (duration < 1 || duration > 60) {
      const response: PomodoroApiResponse = {
        success: false,
        error: "Duration must be between 1 and 60 minutes",
      };
      return NextResponse.json(response, { status: 400 });
    }

    // Start new Pomodoro session
    const newState = await updatePomodoroState({
      isActive: true,
      timeLeft: duration * 60, // Convert minutes to seconds
      mode: "work",
      startTime: Date.now(),
      initiatedBy: username.toLowerCase(),
    });

    // Emit to all connected clients
    const io = getSocketIO();
    if (io) {
      io.emit("pomodoroStarted", newState);
    }

    const response: PomodoroApiResponse = {
      success: true,
      data: newState,
      message: `Pomodoro started for ${duration} minutes`,
    };

    return NextResponse.json(response);
  } catch (error) {
    console.error("‚ùå Start Pomodoro error:", error);
    const response: PomodoroApiResponse = {
      success: false,
      error: "Failed to start Pomodoro",
    };
    return NextResponse.json(response, { status: 500 });
  }
}
